import React from "react";
import Popup from "reactjs-popup";

import { useSelector, useDispatch } from "react-redux";
import { addItem, selectCartItemById } from "../../redux/slices/cartSlice";

import { ICartItem, ISushiBlockProps } from "../../data/declarations";
export const SushiBlock: React.FC<ISushiBlockProps> = ({
  id,
  title,
  price,
  imageUrl,
  description,
  weight,
  nutritionFacts,
}) => {
  const dispatch = useDispatch();
  const cartItem = useSelector(selectCartItemById(id));
  const { calories, protein, fat, carbohydrate } = nutritionFacts;

  const addedCount = cartItem ? cartItem.count : 0;

  const onClickAdd = () => {
    const item: ICartItem = {
      id,
      title,
      price,
      imageUrl,
      description,
      weight,
      count: 0,
    };
    dispatch(addItem(item));
  };

  const POSITION_TYPES = ["left top"];

  return (
    <div className="sushi-block">
      <img className="sushi-block__image" src={imageUrl} alt="" />
      <h4 className="sushi-block__title">
        {title} <span className="sushi-block__weight">{weight} </span>
        {POSITION_TYPES.map((position, i) => (
          <Popup
            key={`tp-${i}`}
            trigger={<button type="button" className="button__info"></button>}
            // @ts-ignore
            position={position}
            on={["click"]}
            // arrow={position !== "center center"}
          >
            <table className="sushi-block__calories">
              <tbody>
                <tr>
                  <th colSpan={3}>
                    <b>   100 </b>
                  </th>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <b>{calories}</b>
                  </td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <b>{protein}</b>
                  </td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <b>{fat}</b>
                  </td>
                  <td></td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <b>{carbohydrate}</b>
                  </td>
                  <td></td>
                </tr>
              </tbody>
            </table>
          </Popup>
        ))}
      </h4>
      <div className="sushi-block__selector">
        <p>{description}</p>
        <div className="sushi-block__bottom">
          <div className="sushi-block__price"> {price} </div>
          <button
            onClick={onClickAdd}
            className="button button--outline button--add"
          >
            <span></span>
            {addedCount > 0 && <i>{addedCount}</i>}
          </button>
        </div>
      </div>
    </div>
  );
};
